export default {
	data() {
		return {
			bian: false
		};
	},
	methods: {
		//点击公司名称显示隐藏下列内容
		xian: function() {
			this.bian = !this.bian;
		},
		//跳转页面
		opennew: function(id) {
			uni.navigateTo({
				url: '../' + id + '/' + id
			});
		},
		//流标
		liubiao: function(id) {
			uni.showModal({
				title: '温馨提示',
				content: '确定流标此求购单吗',
				confirmColor: '#008A05',
				success: res => {
					if (res.confirm) {
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
	}
};
